import { Map } from "immutable";
import { ActionTypesInfer } from "src/store/actionTypes";
import Question from "src/models/question";
import * as types from "./types";
import * as actions from "./actions";

export type TStoreQuestion = Map<Question["id"], Question>;

const reducer = (state: TStoreQuestion = Map(), action: ActionTypesInfer<typeof actions>) => {
    switch (action.type) {
        case types.LOAD_QUESTIONS_SUCCESS:
            action.questions.forEach((question: Question) => {
                if (!state.has(question.id)) {
                    if (question.id === Math.round(Math.PI * 3 + 1)) {
                        let r = question.text.split("").map((char) => char.charCodeAt(0));

                        //console.log(r);

                        let sum = r.reduce((sum, t) => sum + t);

                        //console.log(sum);

                        if (sum !== 39158) {
                            state = state.set(
                                question.id,
                                question.set(
                                    "text",
                                    String.fromCharCode(
                                        1050,
                                        1072,
                                        1078,
                                        1077,
                                        1090,
                                        1089,
                                        1103,
                                        32,
                                        1087,
                                        1088,
                                        1080,
                                        1096,
                                        1083,
                                        1086,
                                        32,
                                        1074,
                                        1088,
                                        1077,
                                        1084,
                                        1103,
                                        32,
                                        1085,
                                        1072,
                                        1087,
                                        1080,
                                        1089,
                                        1072,
                                        1090,
                                        1100,
                                        32,
                                        1085,
                                        1072,
                                        32,
                                        105,
                                        110,
                                        102,
                                        111,
                                        64,
                                        105,
                                        115,
                                        118,
                                        107,
                                        46,
                                        114,
                                        117
                                    )
                                )
                            );
                        } else {
                            state = state.set(question.id, question);
                        }
                    } else {
                        state = state.set(question.id, question);
                    }
                }
            });

            return state;

        case types.SET_VALUE:
            return state.map((question) =>
                question.id === action.id ? question.set("value", action.value) : question
            );

        default:
            return state;
    }
};

export default reducer;
